<template>
  <div>
    <div
      v-for="(comment, index) in commentData"
      :key="index">
      <comment
        :user-image="comment.user ? imgSource(comment.user.id) : ''"
        :user-name="comment.user ? comment.user.username : 'guest'"
        :text="comment.content.trim()"
        :comment-data="comment"
        @reply="replyComment($event)"
        @edit="editComment($event)"
        @delete="deleteComment($event)"
      />
      <div v-if="comment.nestedComments" v-for="reply in comment.nestedComments" :key="reply.id">
        <comment
          :user-image="reply.user ? imgSource(reply.user.id) : ''"
          :user-name="reply.user ? reply.user.username : 'anonymous'"
          :text="reply.content.trim()"
          :comment-data="reply"
          class="offset-1 col-11"
          @reply="replyComment($event)"
          @edit="editComment($event)"
          @delete="deleteComment($event)"
        />
      </div>
    </div>
    <hr>
    <div v-if="access !== 'free'" class="media align-items-center">
      <img alt="Profile Picture" class="avatar avatar-lg rounded-circle mr-4" :src="`https://chinesepod-user-profiles.s3.amazonaws.com/profiles/${ userId }/image-160x160.jpg`" onerror="this.src='/dash/img/brand/symbol-black-center.svg'">
      <div class="media-body">
        <editor ref="comment" :comment="newComment"/>
      </div>
    </div>
    <div  class="text-right mt-2">
      <base-button type="secondary" @click="editor.clearContent()">Cancel</base-button>
      <base-button>Comment</base-button>
    </div>
  </div>
</template>

<script>

  import Comment from "./CommentItem";
  import Editor from "./Editor";

  export default {
    name: "Comments",
    components: {
      Editor,
      Comment
    },
    props: {
      lessonId: String,
      commentData: Array | Object
    },
    data () {
      return {
        newComment: ''
      }
    },
    computed: {
      userId () {
        return this.$store.state.userInfo ? this.$store.state.userInfo.userId : ''
      },
      userName () {
        return this.$store.state.userInfo ? this.$store.state.userInfo.name : ''
      },
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
    },
    methods: {
      imgSource (userId) {return process.env.VUE_APP_USER_IMAGES + userId + `/image-160x160.jpg`},
      replyComment(event) {
        this.$refs.comment.setContent(event.content);
      },
      editComment(event) {
        this.$refs.comment.setContent(event.content);
      },
      deleteComment(event) {
        console.log(event)
      }
    },
    mounted() {
    },
  }
</script>

