<script>
  import fallbackLogo from '@/assets/img/brand/symbol-black-center.svg';

  export default {
    name: 'comment',
    props: {
      userImage: {
        type: String,
        default: '/dash/img/brand/symbol-black-center.svg'
      },
      userId: String,
      userName: {
        type: String,
      },
      text: {
        type: String,
      },
      likeCount: {
        type: Number,
        default: 0
      },
      shareCount: {
        type: Number,
        default: 0
      },
      commentData: Object
    },
    computed: {
      admin() {return this.$store.state.userInfo.access === 'admin'},
      ownComment () {return this.$store.state.userInfo.userId === this.userId},
    },
    methods: {
    },
    data() {
      return {
        fallbackLogo: fallbackLogo,
      }
    }
  }
</script>
